<template>
  <div class="post">
    <van-nav-bar title="订单" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div class="post-list border-bottom">
      <i class="line"></i>
      <div class="post-item" :class="{'act':active==0}">
        <span @click="changePost(0,$event)">全部订单</span>
      </div>
      <div class="post-item" :class="{'act':active==1}">
        <span @click="changePost(1,$event)">待收货</span>
      </div>
      <div class="post-item" :class="{'act':active==2}">
        <span @click="changePost(2,$event)">已收货</span>
      </div>
    </div>
    <div class="post-main">
      <div class="main-item" v-for="(item,index) in orderList" :key="index">
        <div class="post-head">
          <div>{{item.oid|formatOid}}</div>
          <div class="state">
            <van-button
              @click="receive(item,index)"
              v-if="item.status == 1"
              size="small"
              type="primary"
            >确认收货</van-button>
            <van-icon
              @click="removeOrder(item,index)"
              v-if="item.status == 2"
              class="icon-delete"
              name="delete"
            />
          </div>
        </div>
        <div class="post-body">
          <div class="post-item" v-for="(v,j) in item.result" :key="j">
            <div class="img" :style="{backgroundImage:'url('+ v.smallImg +')'}"></div>
            <div class="title">
              <div class="top">{{v.name}}</div>
              <div class="rule">{{v.rule}}</div>
            </div>
            <div class="count">x{{v.count}}</div>
            <div class="price">￥{{v.price}}</div>
          </div>
        </div>
        <div class="post-foot">
          <div>{{item.orderTime|formatDate}}</div>
          <div>
            <span>共{{item.result.length}}件商品</span>
            <span class="price">￥{{item.totalPrice}}</span>
          </div>
        </div>
      </div>
      <div class="tag" v-show="orderList.length == 0">没有订单</div>
    </div>
  </div>
</template>

<script>
import Api from "../api/api";
import Util from "../util/utils";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      active: 0
    };
  },
  created() {
    //在这里发起请求
    let tokenString = Util.getCookies(this);
    Util.showLoading(this);
    Api.findOrder({ tokenString, status: 0 }).then(res => {
      if (res.data.code == 70000) {
        this.$toast(res.data.msg);
        let result = res.data.result;
        // 在这里构造好数据，分好组 orderList:[{result:[]} ,{} ,{},{}]
        let orderList = [];
        let oids = []; //记录key值
        result.forEach(v => {
          if (!oids.includes(v.oid)) {
            oids.push(v.oid);
          }
        });
        oids.forEach(v => {
          let obj = {};
          // 再加一个oid
          obj.oid = v;
          obj.result = [];
          obj.totalPrice = 0;
          result.forEach(v1 => {
            if (v1.oid == v) {
              obj.status = v1.status;
              obj.orderTime = new Date(v1.createdAt);
              obj.result.push(v1);
              obj.totalPrice += (v1.count * 100 * v1.price) / 100;
            }
          });
          obj.totalPrice = obj.totalPrice.toFixed(2);
          orderList.push(obj);
        });
        orderList.sort((a, b) => {
          return new Date(b.orderTime) - new Date(a.orderTime);
        });
        this.changeData({ key: "orderList", value: orderList });
      }
    });
  },
  mounted() {
    let line = document.querySelector(".line");
    let ele = document.querySelector(".post-list span");
    line.style.width = ele.offsetWidth + "px";
    line.style.left = ele.offsetLeft + "px";
  },
  computed: {
    ...mapState("postModule", ["orderList"])
  },
  methods: {
    ...mapMutations("postModule", ["changeData", "deleteItem"]),
    onClickLeft() {
      this.$router.go(-1);
    },
    changePost(index, e) {
      // 这里是改变样式
      if (this.active == index) {
        return;
      }
      let line = document.querySelector(".line");
      line.style.width = e.target.offsetWidth + "px";
      line.style.left = e.target.offsetLeft + "px";
      this.active = index;

      //在这里发起请求
      let tokenString = Util.getCookies(this);
      Util.showLoading(this);
      Api.findOrder({ tokenString, status: index }).then(res => {
        if (res.data.code == 70000) {
          this.$toast(res.data.msg);
          let result = res.data.result;
          // 在这里构造好数据，分好组 orderList:[{"oid":[]} ,{} ,{},{}]
          let orderList = [];
          let oids = []; //记录key值
          result.forEach(v => {
            if (!oids.includes(v.oid)) {
              oids.push(v.oid);
            }
          });
          oids.forEach(v => {
            let obj = {};
            // 再加一个oid
            obj.oid = v;
            obj.result = [];
            obj.totalPrice = 0;
            result.forEach(v1 => {
              if (v1.oid == v) {
                obj.status = v1.status;
                obj.result.push(v1);
                obj.orderTime = new Date(v1.createdAt);
                obj.totalPrice += (v1.count * 100 * v1.price) / 100;
              }
            });
            obj.totalPrice = obj.totalPrice.toFixed(2);

            orderList.push(obj);
          });
          orderList.sort((a, b) => {
            return new Date(b.orderTime) - new Date(a.orderTime);
          });
          // 初始化订单列表
          this.changeData({ key: "orderList", value: orderList });
        }
      });
    },
    receive(item, index) {
      // 这里是确认收货
      let tokenString = Util.getCookies(this);

      Util.showLoading(this);

      let oid = item.oid;
      Api.receive({ tokenString, oid }).then(res => {
        if (res.data.code == 80000) {
          this.$toast(res.data.msg);

          // 后端处理完，前台处理
          // 分两种情况
          // 在全部订单时候 1.0遍历item,把status修改
          if (this.active == 0) {
            item.status = 2;
            // 当现在是全部订单的时候
            item.result.forEach(v => {
              v.status = 2;
            });
          } else {
            // 直接删除这个item
            this.deleteItem({ index });
          }
        }
      });
    },
    removeOrder(item, index) {
      // 这里是删除货物
      let tokenString = Util.getCookies(this);
      Util.showLoading(this);
      let oid = item.oid;
      Api.removeOrder({ tokenString, oid }).then(res => {
        if (res.data.code == 90000) {
          this.$toast(res.data.msg);
          // 后端删除后，到前端删除
          this.deleteItem({ index });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/util.scss";
/deep/ .van-nav-bar {
  .van-icon-arrow-left {
    color: #7f5839;
  }
  .van-nav-bar__text {
    color: #7f5839;
  }
}

/deep/ .van-button--primary {
  background-color: #7f5839;
  border-color: #7f5839;
}

.icon-delete {
  font-size: 25px;
  position: relative;
  top: 5px;
}

.tag {
  height: 500px;
  line-height: 500px;
  font-size: 30px;
  text-align: center;
}

.post {
  height: 100%;
}
.post-list {
  width: 100%;
  display: flex;
  position: relative;
  .line {
    position: absolute;
    left: 0;
    bottom: 5px;
    background-color: #7f5839;
    transition: all 0.2s ease;
    width: 0;
    height: 2px;
  }
  .act {
    span {
      font-weight: 600;
    }
    color: #7f5839;
  }
  .post-item {
    width: 33.33%;
    text-align: center;
    font-size: 16px;
    height: 40px;
    line-height: 40px;
  }
}

.post-main {
  height: calc(100% - 86px);
  padding: 20px 0;
  box-sizing: border-box;
  overflow-y: auto;
}
.main-item {
  margin: auto;
  width: 300px;
  border-radius: 10px;
  box-shadow: 0 0 1px 1px #eee;
  padding: 10px 20px;
  margin-bottom: 10px;
}
.post-head,
.post-foot {
  display: flex;
  font-size: 16px;
}
.post-head {
  border-bottom: 1px solid #eee;
  height: 35px;
  line-height: 35px;
  .state {
    color: #999;
  }
}
.post-body {
  font-size: 16px;
  .post-item {
    margin: 20px 0;
    height: 60px;
    line-height: 60px;
    display: flex;
  }
  .title {
    width: 130px;
    padding: 0 10px;
    .top {
      height: 40px;
      line-height: 40px;
      font-weight: 400;
      font-size: 16px;
    }
    .rule {
      height: 20px;
      line-height: 20px;
      font-size: 12px;
      color: #999;
    }
  }
  .img {
    padding: 30px;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 10px;
  }
  .count {
    width: 30px;
    text-align: center;
    color: #999;
  }
  .price {
    width: 60px;
    font-weight: 600;
    text-align: right;
  }
}

.post-head {
  justify-content: space-between;
}
.post-foot {
  justify-content: space-between;
  flex-wrap: nowrap;
  font-size: 12px;
  height: 35px;
  line-height: 35px;
  color: #999;
  div {
    flex-shrink: 0;
  }
  span {
    margin-left: 10px;
  }
  .price {
    font-size: 18px;
    font-weight: 600;
    color: #000;
  }
}
</style>